.grid {
  width: 100%;
  padding: 0px 20px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

@media (max-width: 900px) {
  .showcase-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
