/* SCROLLBAR */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: #eee;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(255, 0, 0, 0.2);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 0, 0, 0.8);
}

.loading {
  display: flex;
  font-size: 2rem;
  justify-content: center;
  align-items: center;
  padding: 20px;
  height: 500px;
}
.error {
  display: flex;
  justify-content: center;
  padding: 20px;
  height: 10px;
}
.card-container {
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 400px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.2);
  margin: 20px;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.2);
  background-color: #f6f6f6;
  transition: transform 0.2s ease;
}

.card-container:hover .card-image img {
  transform: scale(1.05);
}
.card-image {
  display: flex;
  align-items: center;
  width: 100%;
  height: 50%;
  overflow: hidden;
}
.card-image img {
  width: 100%;
  transition: transform 0.2s ease;
}
.card-info {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 50%;
}
.card-title {
  padding: 10px;
}
.card-category {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 6px 12px;
}
.card-area {
  border: 1px solid #ccc;

  border-radius: 10px;
  padding: 6px 12px;
}
.icon {
  margin-right: 10px;
}
.heart {
  transition: all 0.2s ease;
}
.grey {
  color: #bbb;
}
.pink {
  color: #f54e59;
}
.heart:hover {
  color: #f54e59;
}

@keyframes heartAnimation {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}


.modal-bottom-container {
  position: relative;
  margin-top: 70px;
  bottom: 0;

  width: 200%;
}
.svg-background-modal-top {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.svg-background-modal-bottom {
  position: absolute;
  bottom: 0;
  z-index: 1;
}
.modal-backdrop {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2000;
}
.close-btn {
  position: absolute;
  top: 5%;
  right: 4%;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 2;
}
.close-btn:hover {
  opacity: 0.7;
}
.modal-container {
  position: relative;
  width: 95%;
  height: 100vh;
  /* background-color: rgba(255, 255, 255, 0.9); */
  background-color: #f6f6f6;
  padding: 50px 200px 0px 200px;
  /* CHANGE PADDING IN MEDIA QUERY */
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  overflow-y: auto;
}
.modal-img-container {
  display: block;
  max-width: 400px;
  max-height: 400px;
  border: 1px solid #ccc;
  /* border-radius: 50%; */
  background-color: #ccc;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.3);
  margin-bottom: 10px;
  z-index: 2;
}
.modal-img-container img {
  width: 100%;
}
.meal-title {
  text-transform: uppercase;
  letter-spacing: 3px;
  font-size: 2.5rem;
  margin: 30px;
  width: 100%;
  text-align: center;
  padding: 20px 0 20px 0;
  background-color: #f54e59;

  color: white;
  /* border-bottom: 3px solid #f54e59; */
}
.meal-info {
  position: absolute;
  top: 5%;
  left: 4%;
  display: flex;
  flex-direction: column;
  /* display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; */
}
.flag {
  width: 50px;
  height: 50px;
  z-index: 2;
}
.food-icon {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
  z-index: 2;
}
.flag img {
  width: 100%;
}
.ingredients-container {
  width: 100%;
}
.ingredients-title {
  text-transform: uppercase;
  font-size: 1.2rem;
  letter-spacing: 2px;
  padding-top: 10px;
  margin-bottom: 40px;
}
.ingredients-container ul {
  width: 100%;
  list-style: none;
}
.ingredient1,
.measure1 {
  text-transform: uppercase;
  letter-spacing: 3px;
}
.ingredients-container ul li {
  /* border: 1px solid #ccc; */
  background-color: #eee;
  margin: 8px 0;
  padding: 5px 100px;
  display: flex;
  justify-content: space-between;
  font-size: 0.9rem;
  border-left: 3px solid #f54e59;
}
.instructions-title {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 50px 0;
}
.instructions-container {
  background-color: #eee;
}
.instructions {
  padding: 50px;
  border-left: 3px solid #f54e59;
  letter-spacing: 2px;
  line-height: 28px;
}
.buon-appetito {
  font-family: "Dancing Script", cursive;
  margin: 100px 0;
  color: #f54e59;
  font-size: 3rem;
}
.video {
  width: 100%;
  height: 500px;
  z-index: 5;
}
.recipe-source {
  margin: 30px 0 0px 0;
  z-index: 5;
}
.recipe-source a {
  color: #f54e59;
  padding: 5px 10px;
  cursor: pointer;
  transition: 0.2s all ease;
  border-bottom: 1px solid transparent;
  z-index: 5;
}
.recipe-source a:hover {
  border-bottom: 1px solid #f54e59;
}

/* media queries */

@media (max-width: 900px) {
  .modal-container {
    padding: 50px 40px 0px 40px;
  }
  .modal-img-container {
    /* max-width: 300px;
    max-height: 300px; */
    margin: 10px;
    width: 350px;
  }
  .food-icon {
    width: 40px;
    height: 40px;
    margin-bottom: 10px;
  }
  .flag {
    width: 40px;
    height: 40px;
  }
  .meal-title {
    letter-spacing: 2px;
    font-size: 2rem;
    margin: 30px;
    padding: 15px 0 15px 0;
  }
  .ingredient1,
  .measure1 {
    letter-spacing: 2px;
  }
  .ingredients-container ul li {
    padding: 5px 30px;
  }
  .instructions {
    padding: 30px;
    letter-spacing: 1px;
    line-height: 27px;
  }
}
@media (max-width: 500px) {
  .modal-img-container {
    /* max-width: 300px;
    max-height: 300px; */
    margin: 10px;
    width: 300px;
  }
  .modal-container {
    width: 100%;
    overflow-x: hidden;
  }
  .ingredients-title {
    font-size: 1rem;
  }
  .ingredient1,
  .measure1 {
    letter-spacing: 1px;
  }
  .ingredients-container ul li {
    margin: 8px 0;
    padding: 5px 20px;
    justify-content: space-between;
    font-size: 0.7rem;
  }
  .instructions {
    font-size: 0.9rem;
    padding: 20px;
    letter-spacing: 0px;
    line-height: 27px;
  }
  .buon-appetito {
    font-size: 2.2rem;
  }
  .video {
    height: 400px;
  }
}
@media (max-width: 400px) {
  .modal-img-container {
    margin: 10px;
    width: 250px;
  }
}

